.page-header{
  background-color: rgba(221,221,221,0.7);
  display: flex;
  position: fixed;
  width: 100%;
  height: 100px;
  padding:0px;
  margin: 0px;
  border: 0px;
}
.page-title{
  margin: 0px;
  font-size: 300%;
  color: #432;
  text-align: center;
  padding-top: 100px;
}

.main-nav{
  text-transform: uppercase;
  padding: 0px;
  list-style: none;
  justify-content: space-around;
  display: flex;
  margin-top: 30px

}
.navbarLink{
  text-align: center;
  color: #432;
  font-size: 1.7rem;
}
.nav-list{
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
}
li{
  text-align: center;
} 

@media(max-width: 900px){
  .page-header{
    background-color: rgba(221,221,221,0.7);
    display: flex;
    justify-content: center;
    position: fixed;
    width: 100%;
    height: 200px;
    padding:0px;
    margin: 0px;
    border: 0px;
  }
  .page-title{
    margin: 0px;
    font-size: 200%;
    color: #432;
    text-align: center;
    padding-top: 200px;
  }
  .main-nav{
    display: grid;
    grid-template-columns: 0.5fr 0.5fr;
    font-size: 1.5rem;
    text-transform: uppercase;
    padding: 0px;
    list-style: none;
    justify-content: space-around;
  }
  .navbarLink{
    text-align: center;
    color: #432;
    font-size: 1.5rem;
  }
  .nav-list{
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
  }
 li{
    text-align: center;
  } 
}

