.container {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    height: 100vh
}
.background-container{
    display: grid;
    grid-template-columns: 100%;
    margin-left: 45%;
}
.background-title{
    font-size: 3rem;
    margin:0;
    padding-top: 60vh;
    color: white;
}
.background-text{
    font-size: 1rem;
    margin:0;
    padding-right: 5%;
    padding-top: 0px;
    text-align: justify;
    color: white;
}

