        display: grid;
        grid-template-columns: 100%;
        margin-left: 5%;
    }
    .background-title{
        font-size: 2rem;
        color: white;
        padding-top: 30vh;
        text-align: left;
    }
    .background-text{
        font-size: 0.8rem;
        padding-top: 0px;
        text-align: left;
        color: white;

    }
}
